"use client"

import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts"
import { TrendingDown, TrendingUp } from "lucide-react"

interface Product {
  id: string
  name: string
  image: string
  priceHistory: { date: string; price: number }[]
}

interface PriceHistoryModalProps {
  isOpen: boolean
  onClose: () => void
  product: Product
}

export function PriceHistoryModal({ isOpen, onClose, product }: PriceHistoryModalProps) {
  const currentPrice = product.priceHistory[product.priceHistory.length - 1]?.price || 0
  const oldestPrice = product.priceHistory[0]?.price || 0
  const priceChange = currentPrice - oldestPrice
  const priceChangePercent = ((priceChange / oldestPrice) * 100).toFixed(1)
  const isDecreasing = priceChange < 0

  const formattedData = product.priceHistory.map((item) => ({
    ...item,
    displayDate: new Date(item.date).toLocaleDateString("pt-BR", {
      month: "short",
      year: "numeric",
    }),
  }))

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-3xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="text-2xl text-balance">{product.name}</DialogTitle>
        </DialogHeader>

        <div className="space-y-6">
          {/* Price Summary */}
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
            <div className="bg-muted rounded-lg p-4">
              <p className="text-sm text-muted-foreground mb-1">Preço Atual</p>
              <p className="text-2xl font-bold text-primary">R$ {currentPrice.toFixed(2).replace(".", ",")}</p>
            </div>

            <div className="bg-muted rounded-lg p-4">
              <p className="text-sm text-muted-foreground mb-1">Preço Inicial</p>
              <p className="text-2xl font-bold">R$ {oldestPrice.toFixed(2).replace(".", ",")}</p>
            </div>

            <div className="bg-muted rounded-lg p-4">
              <p className="text-sm text-muted-foreground mb-1">Variação</p>
              <div className="flex items-center gap-2">
                {isDecreasing ? (
                  <TrendingDown className="w-5 h-5 text-accent" />
                ) : (
                  <TrendingUp className="w-5 h-5 text-destructive" />
                )}
                <p className={`text-2xl font-bold ${isDecreasing ? "text-accent" : "text-destructive"}`}>
                  {isDecreasing ? "" : "+"}
                  {priceChangePercent}%
                </p>
              </div>
            </div>
          </div>

          {/* Chart */}
          <div className="bg-card rounded-lg p-4 border">
            <h3 className="text-lg font-semibold mb-4">Histórico de Preços</h3>
            <ResponsiveContainer width="100%" height={300}>
              <LineChart data={formattedData}>
                <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                <XAxis dataKey="displayDate" className="text-xs" tick={{ fill: "hsl(var(--muted-foreground))" }} />
                <YAxis
                  className="text-xs"
                  tick={{ fill: "hsl(var(--muted-foreground))" }}
                  tickFormatter={(value) => `R$ ${value}`}
                />
                <Tooltip
                  contentStyle={{
                    backgroundColor: "hsl(var(--popover))",
                    border: "1px solid hsl(var(--border))",
                    borderRadius: "8px",
                  }}
                  formatter={(value: number) => [`R$ ${value.toFixed(2).replace(".", ",")}`, "Preço"]}
                />
                <Line
                  type="monotone"
                  dataKey="price"
                  stroke="hsl(var(--primary))"
                  strokeWidth={3}
                  dot={{ fill: "hsl(var(--primary))", r: 4 }}
                  activeDot={{ r: 6 }}
                />
              </LineChart>
            </ResponsiveContainer>
          </div>

          {/* Product Image */}
          <div className="flex justify-center">
            <div className="w-48 h-48 rounded-lg overflow-hidden bg-muted">
              <img
                src={product.image || "/placeholder.svg"}
                alt={product.name}
                className="w-full h-full object-cover"
              />
            </div>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  )
}
